import { styles } from "@wordpress/icons";
import { createHigherOrderComponent } from "@wordpress/compose";

import { useState, useEffect } from "@wordpress/element";
import { useEntityRecords } from "@wordpress/core-data";
import { PanelBody, Modal, Button, PanelRow } from "@wordpress/components";
import { InspectorControls } from "@wordpress/block-editor";
import { addFilter } from "@wordpress/hooks";
import apiFetch from "@wordpress/api-fetch";
import { __ } from "@wordpress/i18n";

import TokenMultiSelectControl from "./token-multiselect-control";
import ViewBlockStyles from "./ViewBlockStyles";
import EditBlockStyle from "./EditBlockStyle";

import "./style.scss";

// Block Styles Manager Plugin Component
const BlockStylesManagerPlugin = () => {
	const [modalView, setModalView] = useState("");
	const [currentBlockStyle, setCurrentBlockStyle] = useState(null);
	const [blockStyles, setBlockStyles] = useState([]);
	const [selectedBlockStyles, setSelectedBlockStyles] = useState([]);

	const { records, hasResolved } = useEntityRecords(
		"postType",
		"wpdev_block_style",
		{ per_page: -1 },
	);

	const launchEditForm = (id) => {
		let blockStyle = blockStyles.find(
			(blockStyle) => blockStyle.id === parseInt(id),
		);
		setCurrentBlockStyle({
			...blockStyle,
			title: blockStyle.title.raw,
			content: blockStyle.content.raw,
		});
		setModalView("edit");
	};

	// @todo - grab the current block type as default here
	const newBlockStyle = {
		id: 0,
		title: "New Block Style",
		slug: "is-style-new-block-style",
		content: "selector {\n  opacity: 0.5;\n}",
		meta: {
			block_types: ["core/group"],
		},
	};
	const filterSelector = (css, record) => {
		return css.replace(/selector/g, `.is-style-${record.slug}`);
	};

	useEffect(() => {
		if (records) {
			setBlockStyles(records);
			// Add records CSS to iframe
			let css = "";
			records.forEach((record) => {
				css += filterSelector(record.content.raw, record);
			});
			const style = document.createElement("style");
			style.innerHTML = css;
			style.id = "wpdev-block-styles";
			let destination =
				window.parent.document.querySelector('iframe[name="editor-canvas"]')
					?.document.head ?? document.head;

			destination.appendChild(style);
		}
	}, [records]);

	const AddNewButton = () => {
		return (
			<Button
				onClick={() => {
					console.log("clicked");
					setModalView("new");
				}}
				variant="primary"
			>
				{__("Add New Block Style")}
			</Button>
		);
	};

	return (
		<InspectorControls>
			<PanelBody title={__("Block Styles Manager")} initialOpen={true}>
				{/* <PanelRow>
					<TokenMultiSelectControl
						label={__("Add Styles")}
						value={selectedBlockStyles}
						options={blockStyles.map((blockStyle) => ({
							label: blockStyle.title.rendered,
							value: blockStyle.id,
						}))}
						multiple={true}
						onChange={(blockStyles) => {
							setSelectedBlockStyles(blockStyles);
						}}
					/>
				</PanelRow> */}
				<PanelRow>
					<Button
						icon={styles}
						onClick={() => setModalView("list")}
						aria-expanded={"" !== modalView}
						aria-haspopup="dialog"
						variant="secondary"
					>
						{__("Manage Block Styles")}
					</Button>
				</PanelRow>
			</PanelBody>
			{"" !== modalView && (
				<Modal
					title={__("Block Styles Manager")}
					size="large"
					headerActions={<AddNewButton />}
					isFullScreen={true}
					overlayClassName="wpdev-block-style-manager"
					onRequestClose={() => {
						setModalView("");
					}}
				>
					{modalView === "list" && (
						<ViewBlockStyles
							launchEditForm={launchEditForm}
							records={records}
							hasResolved={hasResolved}
						/>
					)}
					{modalView === "edit" && (
						<>
							<h2>{__("Edit Block Style")}</h2>
							<EditBlockStyle
								attributes={currentBlockStyle}
								closeForm={() => {
									setModalView("list");
								}}
							/>
						</>
					)}
					{modalView === "new" && (
						<>
							<h2>{__("New Block Style")}</h2>
							<EditBlockStyle
								attributes={newBlockStyle}
								closeForm={() => {
									setModalView("list");
								}}
							/>
						</>
					)}
				</Modal>
			)}
		</InspectorControls>
	);
};

/**
 * Add the edit component to the block.
 * This is the component that will be rendered in the editor.
 * It will be rendered after the original block edit component.
 *
 * @param {function} BlockEdit Original component
 * @returns {function} Wrapped component
 *
 * @see https://developer.wordpress.org/block-editor/developers/filters/block-filters/#editor-blockedit
 */
addFilter(
	"editor.BlockEdit",
	"wpdev/block-styles-manager",
	createHigherOrderComponent((BlockEdit) => {
		return (props) => {
			return (
				<>
					<BlockEdit {...props} />
					<BlockStylesManagerPlugin {...props} />
				</>
			);
		};
	}),
);

// On initial load, register all block styles.
apiFetch({
	path: "/wp/v2/wpdev_block_style",
}).then((response) => {
	response.forEach((blockStyle) => {
		console.log(blockStyle);
		wp.blocks.registerBlockStyle(blockStyle.meta.block_type, {
			name: blockStyle.slug,
			label: blockStyle.title.rendered,
			inlineStyle: blockStyle.content.raw,
		});
	});
});
